.grass {
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -20%);
    animation: grass 0.5s;
    height: 45px;
    width: 250px;
}

.girl {
    position: absolute;
    top: 43%;
    left: 50%;
    transform: translate(-50%, -35%);
    height: 100px;
    width: 180px;
    animation: girl 1.6s;
}

.fire {
    position: absolute;
    top: 33%;
    left: 50%;
    transform: translate(-50%, -60%);
    animation: fade-in 7s;
}

.shiksha {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: fade-in 2s;
    animation-delay: 1.8s;
    animation-fill-mode: both;
    width: 450px;
    height: 450px;
}

.man {
    position: absolute;
    height: 180px;
    width: 320px;
    top: 50%;
    left: 50%;
    transform: translate(-53%, -80%);
    animation: fade-in 2s, man 0.5s infinite;
    animation-delay: 1.2s;
    animation-fill-mode: both;
}


