@keyframes fade-in {
    0% {
        opacity: 0;
    }
    ;
    100% {
        opacity: 1;
    }
}

@keyframes grass {
    0% {
        height: 0px;
        width: 0px
    }
    ;
    100% {
        height: 45px;
        width: 300px
    }
    ;
}

@keyframes girl {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    ;
    100% {
        transform: scale(1);
        opacity: 1;
    }
    ;
}

svg {
    position: absolute;
    width: 450px;
    height: 450px;
    transform: translate(-50%, 12.5%);
}

.draw {
    stroke: #000;
    stroke-width: 10px;
    fill: transparent;
    stroke-dasharray: 7000;
    stroke-dashoffset: 7000;
    animation: animate 10s linear infinite;
}

@keyframes animate {
    to {
        stroke-dashoffset: 00;
    }
}

#myDiv {
    display: none;
    width: 100%;
    height: 100%;
    margin: 10vh;
    position: relative;
}

.animate-bottom {
    position: relative;
    animation-name: animatebottom;
    animation-duration: 1s
}

@keyframes animatebottom {
    from {
        bottom: -100px;
        opacity: 0
    }
    to {
        bottom: 0;
        opacity: 1
    }
}